nav {
    display:flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    background-color: #4E9CAF; 
    min-height: 75px;
    align-items: center;
}

.current {
    color: green;
}

.header
{
    min-height: 100px;
    color:  rgb(118, 172, 216);
}

.navlink.current
{
    vertical-align: bottom;
    background-color: darkgrey;
}

.navlink,
.button {    
    width: 115px;
    height: 25px;
    background: #4E9CAF;
    padding: 10px;
    text-align: center;
    border-radius: 5px;
    color: white;
    font-weight: bold;
    line-height: 25px;    
}